import { Inter } from 'next/font/google';
import { Amiri } from 'next/font/google'; // Import the Amiri font
import './globals.css';

const inter = Inter({ subsets: ['latin'], variable: '--font-inter' });

// Configure the Amiri font for Arabic script
const amiri = Amiri({ 
  subsets: ['arabic'], 
  weight: ['400', '700'], // We need normal and bold weights
  variable: '--font-amiri' 
});

export const metadata = {
  title: 'Islam 360 Clone',
  description: 'A project to read the Holy Quran',
};

export default function RootLayout({ children }) {
  return (
    <html lang="en">
      {/* Combine the font variables so we can use them in our CSS */}
      <body className={`${inter.variable} ${amiri.variable} font-sans`}>
        {children}
      </body>
    </html>
  );
}